    let niveau: Niveau = consultForm.controls['niveau'].value;
    let pression = consultForm.controls['pression'].value;
    let cholesterol = consultForm.controls['age'].value;
    let jeun: Jeun = consultForm.controls['jeun'].value;
    let electrocardio: Electrocardio = consultForm.controls['electrocardio'].value;
